
import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/service/data.service';
import { PaginationService } from 'src/app/service/pagination.service';
import { ISubscription } from "rxjs/Subscription";
import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { ActionField, TextField, StatusField } from 'src/app/common-module/table-list/list-field-type';
import { PutAwayService } from 'src/app/service/put-away.service';

@Component({
  selector: 'app-order-putaway',
  templateUrl: './order-putaway.component.html'
})
export class OrderPutawayComponent implements OnInit {

  objectArray: any;
  viewId: any;
  orderProductListArray: Array<any> = [];
  headerData: Array<any> = [];
  loadingState = false;
  pagination: any;
  isGenerate: boolean = false;
  currentPage: number = 1;
  private subscription: ISubscription;

  constructor(
    private dataService: DataService,
    private paginationService: PaginationService,
    private PutAwayService: PutAwayService,
    private location: Location,
    private activatedRoute: ActivatedRoute
  ) {

    this.activatedRoute.parent.params.subscribe((response) => {
      this.viewId = response['id'];
    });
    this.subscription = this.dataService.OrderDetails.subscribe((data) => {
      if (data) {
        this.objectArray = data;
      }
    });
  }

  ngOnInit() {
    this.setHeaderData();
    this.getOrderProductList();
  }

  back() {
    this.location.back();
  }

  setHeaderData() {
    this.headerData.push(

      new TextField({
        label: "Transfer Order No",
        key: 'transfer_order_no',
      }),
      new TextField({
        label: "Case No",
        key: 'case_no',
      }),
      new TextField({
        label: "Location",
        key: '',
      }),
      new TextField({
        label: "Article",
        key: '',
      }),
      new TextField({
        label: "Putaway Quantity",
        key: 'put_away.put_away_qty',
      }),
      new TextField({
        label: "Pick Quantity",
        key: 'put_away.pick_qty',
      }),
      new StatusField({
        label: "Status",
        key: 'status.label',
      }),


    );
    // let canEdit = true;
    // let actions = {};
    // //
    // // View Details
    // if (canEdit) {
    //   actions['view'] = {
    //     path: '/inbound/registeration/add/',
    //     id: 'order_id',
    //     showText: 'Register Product',
    //     // class: 'fa-bell'
    //   }
    // }

    // let actionColom = new ActionField(actions);
    // this.headerData.push(
    //   actionColom
    // );
  }

  onReloadEvent() {
    this.getOrderProductList();
  }

  getOrderProductList() {
    this.PutAwayService.getTransferOrderListById(this.viewId).subscribe((response) => {
      if (response.success && response.data.length > 0) {
        this.orderProductListArray = response.data ? response.data : [];
        if (this.orderProductListArray.length > 0) {
          this.setOrderId();
        }
        this.isGenerate = response.data.is_generate;
        // this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.orderProductListArray = [];
        this.loadingState = false;
        this.isGenerate = false;
        this.pagination = null;
      }
    }, (error) => {
      this.orderProductListArray = [];
      this.isGenerate = false;
      this.loadingState = false;
      this.pagination = null;
    });
  }
  setOrderId() {
    this.orderProductListArray.filter((obj) => {
      obj.order_id = this.viewId;
    });
  }
  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}


